body, ul {
    margin: 0;
    padding: 0;
}

body {
    background: white;
    font-family: 'Karla', sans-serif;
    cursor: default;
}

.challengeimage2 {
    display: none;
}

.solutionimage2 {
    display: none;
}

.researchimage2 {
    display: none;
}

.work-wrapper {
    background: white;
}

.configure-link {
    font-weight: 600;
}

h2 {
    font-weight: normal;
    font-size: 30px;
    margin: 0 auto;
    line-height: 130%;
}

#about-title {
    font-size: 30px;
    /* text-transform: uppercase; */
    font-weight: 400;
    line-height: 170%;
    letter-spacing: 0.3px;
    text-align: left;
}

#jp-title {
    font-family: 'Noto Serif JP', serif;
    font-size: 25px;
    text-align: left;
    letter-spacing: 0.3px;
    font-weight: 400;
    line-height: 180%;
}

#signature {
    max-width: 80%;
    max-height: 80%;
}

#butterfly {
    position: relative;
    margin: 0;
    padding: 0;
    left: 505px;
    bottom: -20px;
    max-width: 70%;
    max-height: 70%;
    transform: translatey(0px);
	animation: float 5s ease-in-out infinite;
}

#home-page-h2 {
    position: relative;
    top: 5px;
}

#about-website {
    font-weight: bold;
    font-size: 30px;
    letter-spacing: 0;
}

#about-text {
    font-size: 16px;
    letter-spacing: 0;
    text-align: justify;
    text-justify: inter-word;
}

.about-lists {
    font-size: 18px;
}

#link-react {
    font-size: 15px;
    color: #1A6ED1;;
}

#link-email {
    font-size: 15px;
    color: #1A6ED1;
}

#link-email:hover {
    color: rgb(153, 153, 153);
}

h3 {
    font-size: 22px;
    line-height: 130%;

}

.center-text {
    text-align:center;
    padding: 10px;
}

.center {
display: block;
margin-left: auto;
margin-right: auto;
}

h4 {
    font-weight: bold;
}

.h3-extra-titles {
    font-size: 28px;
    line-height: 140%;
    font-weight: bold;
    margin: 20px 10px;
}

#skills-title {
    position: relative;
    text-align: center;
    text-transform: uppercase;
    margin: -20px 10px 20px -45px;
}

p {
    line-height: 170%;
    margin-bottom: 25px;
    margin-top: 10px;
    letter-spacing: 0.3px;
    text-align: justify;
    text-justify: inter-word;
    font-size: 16px;
}

#padding {
    padding: 0 60px 0 60px;
}

.margin-up {
    margin: 130px 0 130px -40px;
    line-height: 185%;
}

.citation {
    font-size: 14px;
    display: inline-block;
    position: relative;
    margin: 15px 50px 30px 50px;
}

.figcaption {
    position: relative;
    text-align: left;
    font-size: 11px;
    left: -65px;
    margin: -15px -4px;
    top: -93px;
}

.section-config {
    position: relative;
    display: inline-block;
    margin: 10px 10px 10px 80px;
    line-height: 1.6;
    padding: 0;
}

#jp-list {
    font-family: 'Noto Serif JP', serif;
}

.center-position {
    text-align: center;
}

.about-me {
    font-weight: bold;
    position: relative;
    top: -80px;
    z-index: 1;
    transform: rotate(-7grad);
    right: 370px;
}

#about-content {
    line-height: 170%;
    font-size: 16px;
}

#jp-about-content {
    font-family: 'Noto Serif JP', serif;
    line-height: 210%;
    font-size: 16px;
    letter-spacing: 0.3px;
}

#skills-content {
    line-height: 170%;
    font-size: 16px;
    text-align: center;
}

nav {
    position: fixed;
    z-index: 2;
    width: 100%;
    background: white;
    margin: 0 auto;
    padding-top: 10px;
    padding-bottom: 10px;
}


nav ul li {
    display: inline-block;
    margin: 0 15px;
    padding: 0;
    letter-spacing: 1px;
    font-size: 19px;
    font-weight: normal;
}

nav li a {
    text-decoration: none;
    color: rgb(95, 95, 95);
}

nav li a:hover {
    text-decoration: none;
    color: black;
    transition: background 0.4s ease-in-out, color 0.4s ease-in-out;
}

nav li a.active {
    font-weight: bold;
    color: black;
}

nav ul {
    text-align: right;
    padding: 20px;
}

ul li {
    list-style: none;
    margin: 10px;
}

.text, .title-text {
    text-align: center;  
    letter-spacing: 0.7px;
    line-height: 150%;
}

.content-config {
    font-size: 17px;
}

.title-text {
    font-weight: bold;
}

#more-work, #work-link {
    list-style: none;
    text-align: right;
    margin-right: 10px;
}

#work-link {
    color: black;
    background: white;
    font-size: 22px;
    text-decoration: none;
    display: inline-block;
    padding: 15px 35px;
    transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
    box-shadow: 1px 1px 10px 0 #cacaca;
}

#work-link:hover {
    background: black;
    color: white;
}

#readnext-link {
    padding: 20px;
    box-shadow: 1px 1px 10px 0 #cacaca;
    position: static;
    opacity: 1;
    display: inline-block;
    margin-top: 30px;
    margin-bottom: 30px;
}

#readnext-link:hover {
    background: black;
    color: white;
}

.coming-up {
    position: relative;
    text-align: center;
    margin-top: 100px;
}

#comingsoon-link {
    background: white;
    box-shadow: 1px 1px 10px 0 #cacaca;
    /* border-radius: 10px; */
    padding: 25px 90px;
    position: relative;
    display: inline-block;
    font-size: 20px;
    transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

#comingsoon-link:hover {
    background: black;
    color: white;
}

.list-style {
    text-transform: none;
    list-style: none;
    text-align: center;
}

header {
    background: white;
    position: relative;
    top: 60px;
    /* margin-bottom: 90px; */
    text-align:center;
    padding: 80px;
}

.main-text {
    letter-spacing: 1px;
    font-size: 55px;
    font-weight: bold;
    margin:0;
}

img {
    max-width: 100%;
    height: auto;
}

.home-image {
    border-radius: 10px;
}

#about-image {
    max-width: 100%;
    max-height: 100%;
    position: relative;
    left: -70px;
    top: -90px;
}

#about-portrait {
    max-width: 95%;
    max-height: 95%;
}

#painting-image {
    position: relative;
    max-width: 75%;
    max-height: 75%;
    right: -190px;
    top: 100px;
}

.left-content {
    margin-right: 30px;
}

.right-content {
    margin-left: 30px;
}

.projects {
    background: white;
    margin: 30px 0;
    padding: 50px;
    width:100%;
    height:100%;
}

.skills-section {
    margin: -140px 0 10px;
}

.workpage-projects {
    background:white;
    margin: 30px 0;
    width: 100%;
    height: 100%;
}

.work-page {
    font-weight: 400;
    font-style: normal;
    text-align: left;
    margin: 0 auto;
    list-style: none;
    line-height: 30px;
    color:black;
}

.box {
    border: 1px solid white;
    border-radius: 10px;
    position: relative;
    transition: transform 0.3s ease-in-out;
    width: 100%;
    height: 100%;
}

.image {
    border-radius: 10px;
    max-width: 100%;
    min-width: 100%;
    height: auto !important;
    /* opacity: 1; */
    display: block;
    transition: transform .3s ease-in-out;
    /* backface-visibility: hidden; */
}

