    font-size: 16px;
    color:black;
}

a:hover {
    text-decoration: none;
    color: rgb(73, 73, 73);
}

.link-configure {
    color: rgb(37, 37, 37);
    padding: 10px;
    font-size: 35px;
    position: absolute;
    top: 50%;
