.link-configure:hover {
    color: rgb(255, 255, 255);
}

.button {
    background: transparent; 
    color: black;
    box-shadow: 1px 1px 10px 0 #cacaca;
    padding: 20px;
    line-height: 3;
    transition: all 0.3s ease-in-out;
    /* font-family: freight-big-pro, serif; */
}

.button:hover {
    background: black;
    color: white;
}

.color {
    background: rgb(202, 202, 202, 0.1);
    /* background: rgb(204, 231, 232, 0.2); */
    /* background: rgba(0, 0, 0, 0.02); */
}

.about-color {
    background: rgba(168, 232, 233, 0.2);
}

.display {
    /* background: rgba(255, 254, 175, 0.6); */
    /* background: rgba(0, 0, 0, 0.02); */
    background: rgba(168, 232, 233, 0.2);
    position: relative;
    top: 70px;
}

.align-row {
    position: relative;
    margin-left: 220px;
}

.block {
    margin: 20px 0 50px 0;
    text-align: justify;
    background: rgb(202, 202, 202, 0.1);
    text-justify: inter-word;
    line-height: 1.8;
}

.social-links {
    top: 10px;
    bottom: 10px;
    text-align: center;
    position: relative;
}

.social-icon-list {
    display: inline-block;
    position: relative;
    width: 25px;
    height: 25px;
    left: 190px;
    margin: 15px 15px;
    transition: all 0.3s ease-in-out;
}

.social-icon-list:hover {
    filter: invert(0.5);
}

.footer-copyright {
    position: relative;
    font-weight: normal;
    text-align: center;
    margin: 80px;
    font-size: 15px;
    color: #a5a5a5;
    letter-spacing: -0.2px;
    top: 40px;
}

.right-alignment, #right-alignment {
    text-align: right;
}

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-15px);
	}
	100% {
		transform: translatey(0px);
	}
}

/* Mobile phones */
@media only screen and (min-width: 200px) and (max-width: 823px) { 
    /* Home-page */
    .header {
        top: 0;
        padding: 40px;
    }
    .main-text {
        text-align: center;
        font-size: 30px;
    }
    nav {
        width: 100%;
        position: relative;
    }
    .col-sm {
        margin: 0;
    }
    .button {
        line-height: 6;
    }
    p {
        margin-top: 10px;
        margin-bottom: 0;
    }
    .projects {
        margin: 0;
        padding: 20px;
        line-height: 1.9;
    }
    #work-link {
        font-size: 18px;
    }
    #about-website {
        font-size: 20px;
        text-align: center;
    }
    #about-text {
        text-align: center;
    }
    #signature {
        max-width: 150%;
        position: relative;
        top: 10px;
    }
    /* Work page */
    .link-configure {
        font-size: 15px;
        text-align: center;
        font-weight: bold;
        line-height: 20px;
    }
    .container {
        padding: 0;
    }
    #comingsoon-link {
        font-size: 13px;
        font-weight: 600;
    }
    /* FridgID */
    .align-row {
        margin: 0;
    }
    ul li {
        margin: 0;
    }
    .challengeimage1 {
        display: none;
    }
    .challengeimage2 {
        display: block;
    }
    .solutionimage1 {
        display: none;
    }
    .solutionimage2 {
        display: block;
    }
    .researchimage1 {
        display: none;
    }
    .researchimage2 {
        display: block;
        margin-top: -50px;
    }
    #padding {
        padding: 0;
    }
    .margin-up {
        margin: 0;
        margin-bottom: 30px;
    }
    .center-text {
        padding: 0;
    }
    .citation {
        margin: 30px 10px 30px 10px;
    }
    .block {
        text-align: left;
    }
    /* About-page */
    .col-6 {
        max-width: 100%;
        flex: 0 0 100%;
        margin: 10px;
    }
    #about-image {
        left: 0;
        top: 0;
        width: 100%;
        height: auto;
    }
    .about-me {
        font-weight: bold;
        position: relative;
        top: -280px;
        z-index: 1;
        transform: rotate(-7grad);
        right: -220px;
        font-size: 13px;
    }
    .figcaption {
        text-align: center;
        left: 0;
        top: 15px;
    }
    #skills-title {
        margin: 0;
    }
    .section-config {
        margin: 0;
        margin-bottom: 10px;
        right: -15px;
        top: 10px;
    }
    .h3-extra-titles {
        font-size: 20px;
        margin: 20px;
    }
    .about-lists {
        font-size: 15px;
        margin: 20px;
        line-height: 1.7;
    }
    .social-icon-list {
        left: 110px;
    }
    .col-4 {
        flex: 0 0 100%;
        max-width: 100%;
    }
    #about-portrait {
        margin: 10px;
    }
    
}

/* iPad Pro and iPad */
@media only screen and (min-width: 768px) and (max-width: 1024px)  {

    /* Home-page */
    #signature {
        max-width: 80%;
        position: relative;
        top: 10px;
    }
    /* Work-page */
    .link-configure {
        font-size: 20px;
        text-align: center;
        font-weight: bold;
        line-height: 20px;
    }
    #comingsoon-link {
        font-size: 16px;
        font-weight: 600;
    }
    /* About-page */
    .col-4 {
        flex: 0 0 100%;
        max-width: 60%;
    }
    .social-icon-list {
        left: 280px;
    }
    .about-me {
        font-weight: bold;
        position: relative;
        top: 2px;
        z-index: 1;
        transform: rotate(-7grad);
        right: 200px;
        font-size: 13px;
    }
}
